import React, { useState, useEffect } from "react";
import { Layout } from "../../containers";
import { WrapAuthForm } from "./style";
import {
	TextField,
	OutlinedInput,
	useMediaQuery,
	InputAdornment,
	IconButton,
	FormGroup,
	FormControlLabel,
	Checkbox,
	Typography,
	CircularProgress,
	Switch,
	Box,
} from "@material-ui/core";
import { useAuthContext } from "../../contexts/Auth.context";
import swal from "sweetalert";
import { ButtonCustom } from "../../components";
import { REGISTER_AFFILIATE, LOGIN_GOOGLE } from "../../services/auth.service";
import { mappingForms } from "../../utils/formsUtils";
import { submitRegisterAffiliate } from "./handler";
import bannerImg from "../../assets/img/register.png";

const RegisterAffiliatePage = (props) => {
	const { isAuthenticated } = useAuthContext();

	const [showPassword, setShowPassword] = useState(false);
	const [loading, setLoading] = useState(false);
	const [form, setForm] = useState({
		name: "",
		phone: "",
		email: "",
		// password: "",
		// password_confirmation: "",
		address: "",
		instagram_username: "",
		instagram_follower: "",
		tiktok_username: "",
		tiktok_follower: "",
		youtube_username: "",
		youtube_subscribe: "",
		ratecard: null,
		is_recomend: false,
		improvement: "",
		interest: [],
		otherInterest: "",
		recomend_speaker: "",
	});
	const isMobile = useMediaQuery("(max-width:500px)");

	const handleClickShowPassword = () => {
		setShowPassword(!showPassword);
	};

	const onChange = async (name, value) => {
		console.log(name, value);
		setForm({
			...form,
			[name]: value,
		});
	};

	const onSubmit = async () => {
		setLoading(true);

		form.otherInterest !== "" && form.interest.push(form.otherInterest);
		const res = await submitRegisterAffiliate(form, props.history);
		if (res) {
			setLoading(false);
			form.otherInterest !== "" && form.interest.pop();
		}
	};

	useEffect(() => {
		isAuthenticated && props.history.push("/");
	}, []);

	const InputText = ({ name, label, value, placeholder }) => {
		return (
			<div style={{ marginBottom: "16px" }}>
				<label>{label}</label>
				<OutlinedInput
					id={name}
					placeholder={placeholder}
					fullWidth
					type={"text"}
					value={value}
					size="small"
					onChange={(e) => onChange({ name }, e.target.value)}
				/>
			</div>
		);
	};

	const forms = [
		{
			id: "name",
			label: "Nama Lengkap",
			type: "text",
			required: true,
			value: form.name,
		},
		{
			id: "phone",
			label: "Nomor HP (WA)",
			type: "text",
			required: true,
			value: form.phone,
			desc: "Contoh: +6281234567890",
		},
		{
			id: "email",
			label: "Email",
			type: "text",
			required: true,
			value: form.email,
		},
		// {
		// 	id: "password",
		// 	label: "Password",
		// 	type: "password",
		// 	required: true,
		// 	value: form.password,
		// },
		// {
		// 	id: "password_confirmation",
		// 	label: "Ulangi Password",
		// 	type: "password",
		// 	required: true,
		// 	value: form.password_confirmation,
		// },
		{
			id: "address",
			label: "Alamat Lengkap Pengiriman Produk",
			type: "multiline",
			required: true,
			value: form.address,
		},
		{
			id: "instagram_username",
			label: "Username Instagram",
			type: "text",
			required: true,
			value: form.instagram_username,
		},
		{
			id: "instagram_follower",
			label: "Jumlah Follower Instagram",
			type: "number",
			required: true,
			value: form.instagram_follower,
		},
		{
			id: "tiktok_username",
			label: "Username Tiktok",
			type: "text",
			required: true,
			value: form.tiktok_username,
		},
		{
			id: "tiktok_follower",
			label: "Jumlah Follower Tiktok",
			type: "number",
			required: true,
			value: form.tiktok_follower,
		},
		{
			id: "youtube_username",
			label: "Username Youtube (opsional)",
			type: "text",
			required: false,
			value: form.youtube_username,
		},
		{
			id: "youtube_subscribe",
			label: "Jumlah Subscriber Youtube (opsional)",
			type: "number",
			required: false,
			value: form.youtube_subscribe,
		},
		{
			id: "ratecard",
			label: "Upload Rate Card",
			type: "file",
			required: true,
			value: form.ratecard,
			desc: "Format file: .jpg, .png, .pdf",
		},
		{
			id: "improvement",
			label:
				"Apakah acara seperti ini membantu dan memotivasi Anda melakukan improvement?",
			type: "multiline",
			required: true,
			value: form.improvement,
		},
		{
			id: "recomend_speaker",
			label: "Jika diadakan lagi, siapa pembicara yang ingin dihadirkan?",
			type: "multiline",
			required: true,
			value: form.recomend_speaker,
		},
		// {
		// 	id: "is_recomend",
		// 	label:
		// 		"Apakah Anda bersedia direkomendasikan dan menerima tawaran kolaborasi dengan ribuan brand yang ada dibawah naungan Kampusjualan?",
		// 	type: "checkbox",
		// 	required: true,
		// 	value: form.is_recomend,
		// },
	];

	const interests = [
		"Digital Marketing",
		"Parenting",
		"Customer Service",
		"Iklan & Ads",
	];

	const handleCheckboxChange = (e, interest) => {
		const newInterests = e.target.checked
			? [...form.interest, interest]
			: form.interest.filter((value) => value !== interest);
		setForm({ ...form, interest: newInterests });
	};

	return (
		<Layout withNavbar={false} {...props}>
			{loading && (
				<div
					style={{
						position: "fixed",
						top: 0,
						left: 0,
						width: "100%",
						height: "100%",
						zIndex: 9999,
						backgroundColor: "rgba(0,0,0,0.5)",
						display: "flex",
						justifyContent: "center",
						alignItems: "center",
					}}>
					<CircularProgress color="primary" />
				</div>
			)}
			<WrapAuthForm>
				<div style={isMobile ? { padding: 16 } : {}}>
					<div className="title">{"Register Affiliator"}</div>
					<div>
						<img
							src={bannerImg}
							alt="banner"
							style={{
								width: "100%",
							}}
						/>
					</div>
					<div className="content">{mappingForms(forms, setForm, form)}</div>
					<div style={{ padding: "0 18px", marginBottom: "8px" }}>
						<Typography variant="body2">
							Apakah Anda bersedia direkomendasikan dan menerima tawaran
							kolaborasi dengan ribuan brand yang ada dibawah naungan
							Kampusjualan?
						</Typography>
						<FormGroup row style={{ marginTop: 8 }}>
							<Box sx={{ display: "flex", alignItems: "center" }}>
								<Typography>Tidak</Typography>
								<Switch
									checked={form.is_recomend}
									onChange={(e) => onChange("is_recomend", e.target.checked)}
									name="is_recomend"
									color="primary"
								/>
								<Typography>Ya</Typography>
							</Box>
						</FormGroup>
					</div>
					<div style={{ padding: "0 18px", marginBottom: "40px" }}>
						<Typography variant="body2">
							Jika diadakan lagi, apa tema yang sangat ingin Anda pelajari?
						</Typography>
						<FormGroup row style={{ marginTop: 8 }}>
							{interests.map((interest) => (
								<FormControlLabel
									key={interest}
									control={
										<Checkbox
											checked={form.interest.includes(interest)}
											onChange={(e) => handleCheckboxChange(e, interest)}
											name={interest}
											color="primary"
										/>
									}
									label={interest}
								/>
							))}
						</FormGroup>
						<div className="formInput">
							<TextField
								placeholder="Lainnya"
								variant="outlined"
								value={form.otherInterest}
								onChange={(e) => onChange("otherInterest", e.target.value)}
							/>
						</div>
					</div>
					<ButtonCustom color="primary" onClick={onSubmit}>
						Register
					</ButtonCustom>
					<div style={{ textAlign: "center", marginTop: 16 }}>
						<p>
							Sudah punya akun ? Klik ke halaman <a href={"/login"}>Login</a>
						</p>
					</div>
				</div>
			</WrapAuthForm>
		</Layout>
	);
};

export default RegisterAffiliatePage;
