import React, { useState, useEffect } from "react";
import { Layout } from "../../containers";
import { WrapAuthForm } from "./style";
import {
	// TextField,
	OutlinedInput,
	useMediaQuery,
	InputAdornment,
	IconButton,
	FormGroup,
	FormControlLabel,
	Checkbox,
	Typography,
	CircularProgress,
	Switch,
	Box,
} from "@material-ui/core";
import GoogleLogin from "react-google-login";
import constanta from "../../configs/constanta";
import brandGoogle from "../../assets/brand/google.svg";
import { useAuthContext } from "../../contexts/Auth.context";
import swal from "sweetalert";
import { ButtonCustom } from "../../components";
import { REGISTER_AFFILIATE, LOGIN_GOOGLE } from "../../services/auth.service";
import { mappingForms } from "../../utils/formsUtils";
import Visibility from "@material-ui/icons/Visibility";
import VisibilityOff from "@material-ui/icons/VisibilityOff";
import bannerImg from "../../assets/img/register.png";
import { submitRegisterAffiliate } from "./handler";

const RegisterPage = (props) => {
	const { isAuthenticated } = useAuthContext();

	const [showPassword, setShowPassword] = useState(false);
	const [loading, setLoading] = useState(false);
	// const [otherInterest, setOtherInterest] = useState("");
	const [form, setForm] = useState({
		name: "",
		phone: "",
		email: "",
		// password: "",
		// password_confirmation: "",
		address: "",
		instagram_username: "",
		instagram_follower: "",
		tiktok_username: "",
		tiktok_follower: "",
		youtube_username: "",
		youtube_subscribe: "",
		ratecard: {
			name: "",
		},
		is_recomend: false,
		improvement: "",
		interest: [],
		otherInterest: "",
		recomend_speaker: "",
	});
	const isMobile = useMediaQuery("(max-width:500px)");

	const handleClickShowPassword = () => {
		setShowPassword(!showPassword);
	};

	const onChange = async (name, value) => {
		setForm({
			...form,
			[name]: value,
		});
	};

	const onSubmit = async () => {
		setLoading(true);
		form.otherInterest !== "" && form.interest.push(form.otherInterest);
		const res = await submitRegisterAffiliate(form, props.history);
		if (res) {
			form.otherInterest !== "" && form.interest.pop();
			setLoading(false);
		}
	};

	useEffect(() => {
		isAuthenticated && props.history.push("/");
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, []);

	const forms = [
		{
			id: "name",
			label: "Nama Lengkap",
			type: "text",
			required: true,
			value: form.name,
		},
		{
			id: "phone",
			label: "Nomor HP (WA)",
			type: "text",
			required: true,
			value: form.phone,
			desc: "Contoh: +6281234567890",
		},
		{
			id: "email",
			label: "Email",
			type: "text",
			required: true,
			value: form.email,
		},
		// {
		// 	id: "password",
		// 	label: "Password",
		// 	type: "password",
		// 	required: true,
		// 	value: form.password,
		// },
		// {
		// 	id: "password_confirmation",
		// 	label: "Ulangi Password",
		// 	type: "password",
		// 	required: true,
		// 	value: form.password_confirmation,
		// },
		{
			id: "address",
			label: "Alamat Lengkap Pengiriman Produk",
			type: "multiline",
			required: true,
			value: form.address,
		},
		{
			id: "instagram_username",
			label: "Username Instagram",
			type: "text",
			required: true,
			value: form.instagram_username,
		},
		{
			id: "instagram_follower",
			label: "Jumlah Follower Instagram",
			type: "number",
			required: true,
			value: form.instagram_follower,
		},
		{
			id: "tiktok_username",
			label: "Username Tiktok",
			type: "text",
			required: true,
			value: form.tiktok_username,
		},
		{
			id: "tiktok_follower",
			label: "Jumlah Follower Tiktok",
			type: "number",
			required: true,
			value: form.tiktok_follower,
		},
		{
			id: "youtube_username",
			label: "Username Youtube (opsional)",
			type: "text",
			required: false,
			value: form.youtube_username,
		},
		{
			id: "youtube_subscribe",
			label: "Jumlah Subscriber Youtube (opsional)",
			type: "number",
			required: false,
			value: form.youtube_subscribe,
		},
		{
			id: "ratecard",
			label: "Upload Rate Card",
			type: "file",
			required: true,
			value: form.ratecard,
			desc: "Format file: .jpg, .png, .pdf",
		},
		{
			id: "improvement",
			label:
				"Apakah acara seperti ini membantu dan memotivasi Anda melakukan improvement?",
			type: "multiline",
			required: true,
			value: form.improvement,
		},
		{
			id: "recomend_speaker",
			label: "Jika diadakan lagi, siapa pembicara yang ingin dihadirkan?",
			type: "multiline",
			required: true,
			value: form.recomend_speaker,
		},
		// {
		// 	id: "is_recomend",
		// 	label:
		// 		"Apakah Anda bersedia direkomendasikan dan menerima tawaran kolaborasi dengan ribuan brand yang ada dibawah naungan Idejualan & Kampusjualan?",
		// 	type: "checkbox",
		// 	required: true,
		// 	value: form.is_recomend,
		// },
	];

	const interests = [
		"Digital Marketing",
		"Parenting",
		"Customer Service",
		"Iklan & Ads",
	];

	const handleCheckboxChange = (e, interest) => {
		const newInterests = e.target.checked
			? [...form.interest, interest]
			: form.interest.filter((value) => value !== interest);
		setForm({ ...form, interest: newInterests });
	};

	return (
		<Layout withNavbar={false} {...props}>
			{loading && (
				<div
					style={{
						position: "fixed",
						top: 0,
						left: 0,
						width: "100%",
						height: "100%",
						zIndex: 9999,
						backgroundColor: "rgba(0,0,0,0.5)",
						display: "flex",
						justifyContent: "center",
						alignItems: "center",
					}}>
					<CircularProgress color="primary" />
				</div>
			)}
			<WrapAuthForm>
				<div>
					<img
						src={bannerImg}
						alt="banner"
						style={{
							width: "100%",
							position: "absolute",
							top: -32,
							left: 0,
							maxHeight: 200,
						}}
					/>
				</div>
				<div className="content">
					{mappingForms(forms, setForm, form)}
					<div style={{ padding: "0 18px", marginBottom: "8px" }}>
						<Typography variant="body2">
							Apakah Anda bersedia direkomendasikan dan menerima tawaran
							kolaborasi dengan ribuan brand yang ada dibawah naungan
							Kampusjualan?
						</Typography>
						<FormGroup row style={{ marginTop: 8 }}>
							<Box sx={{ display: "flex", alignItems: "center" }}>
								<Typography>Tidak</Typography>
								<Switch
									checked={form.is_recomend}
									onChange={(e) => onChange("is_recomend", e.target.checked)}
									name="is_recomend"
									color="primary"
								/>
								<Typography>Ya</Typography>
							</Box>
						</FormGroup>
					</div>
					<div style={{ padding: "0 18px", marginBottom: "28px" }}>
						<Typography variant="body2" style={{ marginTop: 16 }}>
							Jika diadakan lagi, apa tema yang sangat ingin Anda pelajari?
						</Typography>
						<FormGroup row style={{ marginTop: 8 }}>
							{interests.map((interest) => (
								<FormControlLabel
									key={interest}
									control={
										<Checkbox
											checked={form.interest.includes(interest)}
											onChange={(e) => handleCheckboxChange(e, interest)}
											name={interest}
											color="primary"
										/>
									}
									label={interest}
								/>
							))}
							<OutlinedInput
								placeholder="Lainnya"
								fullWidth
								value={form.otherInterest}
								onChange={(e) => onChange("otherInterest", e.target.value)}
							/>
						</FormGroup>
					</div>

					<ButtonCustom color="primary" onClick={onSubmit}>
						Daftar Affiliator
					</ButtonCustom>
					<div className="others">
						<div style={{ textAlign: "center", marginTop: 16 }}>
							<p>
								Sudah punya akun ? <a href={"/login"}>Masuk sini</a>
							</p>
						</div>
					</div>
				</div>
			</WrapAuthForm>
		</Layout>
	);
};

export default RegisterPage;
